@mixin family--primary {
    font-family: map-get($family, primary), sans-serif;
}

@mixin p-line-clamp ($i) {
    display: -webkit-box;
    -webkit-line-clamp: #{$i};
    -webkit-box-orient: vertical;
    overflow: hidden;
}

