///////////// FONT SIZE ////////////////////////

@mixin sizeBase{
    font-size: map-get($size, base);
    line-height: map-get($size, line-base);
    @media (max-width: map-get($media, tl)) {
        font-size: map-get($size, base-tl);
        line-height: map-get($size, line-base-tl);
    }
    // @media (max-width: map-get($media, ts)) {
